/* RGB */

$color1: rgba(169,183,192, 1);
$color2: rgba(223, 223, 223, 1);
$color3: rgba(99, 99, 99, 1);
$color4: rgba(219, 194, 143, 1);
$color5: rgba(199,216,198, 1);

$orange: rgba(244, 162, 97, 1);


$foundation-palette: ( primary: #1779ba,
secondary: #767676,
success: #3adb76,
warning: #ffae00,
alert: #cc4b37,
);

$light-gray: #e6e6e6;
$medium-gray: #cacaca;
$dark-gray: #8a8a8a;
$black: #0a0a0a;
$white: #fefefe;

body {
  background-color: $light-gray;
}

footer {
  background-color: $black;
  color: $white;
  padding-top: 1em;
}

button.radius,
.button.radius {
  border-radius: 3px;
}

button.round,
.button.round {
  border-radius: 1000px;
}


$hero-full-screen-bg: url('https://upload.wikimedia.org/wikipedia/commons/0/08/View_of_South_Lincoln.jpg') center center no-repeat;
$hero-full-screen-bg-size: cover;
$hero-full-screen-text-color: $color2;

.hero-full-screen {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  background: $hero-full-screen-bg;
  background-size: $hero-full-screen-bg-size;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  box-shadow: inset 0 0 0 1000px rgba(109,121,147, .5);
  .middle-content-section {
    text-align: center;
    color: $hero-full-screen-text-color;
    border-style: solid;
    border: 1px;
  }

  .top-content-section {
    width: 100%;
  }

  .bottom-content-section {
    padding: 1rem;

    svg {
      height: rem-calc(60);
      width: rem-calc(60);
      fill: $hero-full-screen-text-color;
    }
  }
}

.middle-image{
  background: url('https://cdn.pixabay.com/photo/2018/01/28/10/08/purchase-3113198_960_720.jpg') center center no-repeat;
  background-size: $hero-full-screen-bg-size;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
}

.small-padding{
  padding-top: 1em;
  padding-bottom: 1em;
}

.section-padding {
    padding-top: 2em;
    padding-bottom: 2em;
