$card-product-hover-transition: all 0.2s ease;

.card-product {
  &:hover {
    .card-product-img-wrapper {

      img {
        filter: grayscale(0);
      }

      .button {
        transform: translateY(-3rem);
      }
    }
  }
}

.card-product-img-wrapper {
  margin-bottom: 1.375rem;
  position: relative;
  overflow: hidden;

  .button {
    transition: $card-product-hover-transition;
    background-color: $color1;
    padding: 1rem 0.5rem;
    bottom: -3rem;
    transform: translateY(3rem);
    position: absolute;
    z-index: 2;
    color: $color2;
    margin-bottom: 0;

    &:hover {
      background-color: $color1;
    }
  }

  img {
    transition: $card-product-hover-transition;
    filter: grayscale(1);
    width: 100%;
  }

  .card-product-name {
    font-size: 1.375rem;
  }

  .card-product-price {
    font-weight: bold;
  }

  .card-product-description {
    color: $color5;
    font-size: 0.875rem;
    margin-bottom: 0;
  }
}



blockquote {
      quotes: "“""”""‘""’  ";
      border-left: none;

      &:before {
        content: open-quote;
        color: $dark-gray;
        font-size: 4em;
        line-height: 0.1em;
        vertical-align: -0.4em;
      }

      &:after {
        content: close-quote;
        color: $dark-gray;
        font-size: 4em;
        line-height: 0.1em;
        vertical-align: -0.6em;
      }
      .blockquote-content {
        display: inline;
        color: $black;
      }
      
}


#fixedbutton {
    position: fixed;
    bottom: 0em;
    right: 0em; 
    }

.small i { font-size: 0.8rem; }
.medium i { font-size: 1rem; }
.large i { font-size: 1.2rem; }


.float-center {
  margin:0 auto;
}